import React from 'react';
import logo from './logo.svg';
import './App.css';
import HeaderComponent from "./components/HeaderComponent";
import MenuComponent from "./components/MenuComponent";
import FooterComponent from "./components/FooterComponent";
import NavMenuComponent from "./components/NavMenuComponent";
import {BrowserRouter as Router, Route} from 'react-router-dom'
import ProjectsPage from "./Pages/ProjectsPage";
import PersonalWebsite from "./Projects/PersonalWebsite";
import Itinerary from "./Projects/Itinerary";
import ClubCalendar from "./Projects/ClubCalendar";

function App() {
  return (
      <div className = "page-container">
          <div className="container content-wrap">
              <HeaderComponent/>
              <NavMenuComponent/>
              <Router>
                  <Route exact={true} path={process.env.PUBLIC_URL + '/'} component={MenuComponent} />
                  <Route exact={true} path={process.env.PUBLIC_URL + '/projects'} component={ProjectsPage} />
                  <Route exact={true} path={process.env.PUBLIC_URL + '/projects/personal_website'} component={PersonalWebsite} />
                  <Route exact={true} path={process.env.PUBLIC_URL + '/projects/itinerary_planner'} component={Itinerary} />
                  <Route exact={true} path={process.env.PUBLIC_URL + '/projects/club_calendar'} component={ClubCalendar} />
              </Router>
          </div>
          <FooterComponent/>
      </div>
  );
}

export default App;
